.Global_AboutPageSectionSwitch.Dream {
    border: 1px solid white;
    appearance: none;
    content: 'Dream';
    padding: 1rem;
    background-color: blue;
    cursor: pointer;
}



.Global_AboutPageSectionSwitch.Dream:checked {}

.Global_AboutPageSectionSwitch.Dream:hover {
    appearance: none;
    padding: 1rem;


}

.Global_AboutPageSectionSwitch.Dream::before {
    appearance: none;
    content: 'dream';
    padding: 1rem;
}

.Global_AboutPageSectionSwitch.Dream:checked::before {
    appearance: none;
    content: 'dream';
    padding: 1rem;
}


