/*/////////////////////////////////*/
/*Desktop View*/
/*/////////////////////////////////*/
#Tablet_AboutPageFooterSection,
#Mobile_AboutPageFooterSection {
    display: none;
}


#Global_AboutPageFooterSection {
    margin: 60px 0px 0px 0px;
    height: auto;
    max-width: var(--max-width);
    display: flex;
    flex-direction: column;
    color: red;
    box-shadow: var(--box-shadow);
}

.Global_AboutPageFooterCards {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.Global_AboutPageFooterCategory {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    width: 250px;
    min-width: 150px;
    border: 1px solid black;
    border-radius: 8px;
    box-shadow: var(--box-shadow);
}

.Global_AboutPageFooterCategoryName {
    margin: 0;
    padding: 0;
    text-align: center;
    font-weight: bold;
    text-decoration: none;
}

.Global_AboutPageFooterCategoryNameBar {
    width: 100%;
}

.Global_AboutPageFooterCategoryList {
    margin: 0 5px;
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

}

.Global_AboutPageFooterCategoryItem {
    margin: 10px 0;
    padding: 0;
}

.Global_AboutPageFooterCategoryItemLink {
    margin: 0;
    padding: 0;
    text-decoration: none;

}

.Global_AboutPageFooterCategoryItemLinkText {
    margin: 0;
    padding: 0;
    font-weight: bold;
    font-size: 1rem;
    color: red;
}

@media(max-width:785px) {
    .Global_AboutPageFooterCategoryItemLinkText {
        margin: 0;
        padding: 0;
        word-break: keep-all;
        text-align: center;
        font-weight: bold;
        font-size: 1rem;
        color: red;
    }
}



.Global_AboutPageFooterBoxText {
    margin: 0;
    padding: 0;
}

.Global_github {
    margin: 0;
    padding: 0;
    width: calc(150px);
    height: 29px;
    border-radius: 8px;
    align-items: center;
}

/*/////////////////////////////////*/
/*Tablet View*/
/*/////////////////////////////////*/
@media (max-width:800px) {

    #Global_AboutPageFooterSection,
    #Mobile_AboutPageFooterSection {
        display: none;
    }

    #Tablet_AboutPageFooterSection {
        display: flex;
        flex-direction: column;
        color: yellow;
        box-shadow: var(--box-shadow);
    }

    .Tablet_AboutPageFooterCards {
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }

    .Tablet_AboutPageFooterCategory {
        display: flex;
        flex-direction: column;

        width: 250px;
        align-items: center;
        border: 1px solid black;
        box-shadow: var(--box-shadow);
        border-radius: 20px;
        height: 200px auto;
        padding: 0;
        margin: 10px;

    }

    .Tablet_AboutPageFooterCategoryName {
        margin: 0;
        padding: 0;
        text-decoration: none;
        padding: 0;
        color: purple;
        font-weight: bold;
        font-size: 1.2rem;
    }

    .Tablet_AboutPageFooterCategoryNameBar {
        margin: 0;
        padding: 0;
        width: 100%;
    }

    .Tablet_AboutPageFooterCategoryList {
        margin-top: 20px;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        list-style-type: none;
        padding: 0;
        padding-bottom: 10px;
    }

    .Tablet_AboutPageFooterCategoryItem {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        text-align: center;
        padding-bottom: 5px;
    }

    .Tablet_AboutPageFooterCategoryItemLink {
        margin: 0;
        padding: 0;
        text-decoration: none;
        color: red;
        font-weight: bold;
        font-size: 1rem;
    }

    .Tablet_AboutPageSubList {
        margin: 0;
        padding: 0;
        visibility: hidden;
    }

    .Tablet_github {
        margin: 0;
        padding: 0;
        width: 125px;

        border: 0;
        height: 32px;
        border-radius: 8px;
    }

    .Tablet_AboutPageFooterBox {
        margin: 0;
        padding: 0;
        display: block;
    }
}

/*/////////////////////////////////*/
/*Mobile View*/
/*/////////////////////////////////*/
