        display: flex;
        flex-direction: column;
        color: yellow;
        box-shadow: var(--box-shadow);
    }

    .Mobile_AboutPageFooterCards {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: space-evenly;
    }

    .Mobile_AboutPageFooterCategory {
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        width: 250px;
        min-width: 150px;
        border: 1px solid black;
        border-radius: 8px;
        box-shadow: var(--box-shadow);

    }

    .Mobile_AboutPageFooterCategoryName {
        margin: 0;
        padding: 0;
        text-align: center;
        font-weight: bold;
        text-decoration: none;
        font-size: 1.2rem;
        color: purple;
    }

    .Mobile_AboutPageFooterCategoryNameBar {
        width: 100%;
    }

    .Mobile_AboutPageFooterCategoryList {

        margin: 0 5px;
        padding: 0;
        list-style-type: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

    }

    .Mobile_AboutPageFooterCategoryItem {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        text-align: center;
        padding-bottom: 5px;
    }

    .Mobile_AboutPageFooterCategoryItemLink {
        margin: 0;
        padding: 0;
        text-decoration: none;
        color: red;
        font-weight: bold;
        font-size: 1rem;
    }
    .Mobile_AboutPageFooterCategoryItemLinkText {
        margin: 5px;
        padding: 0;
        font-weight: bold;
        font-size: 1rem;
        color: red;
    }

    .Mobile_AboutPageSubList {
        margin: 0;
        padding: 0;
        visibility: hidden;
    }

    .Mobile_github {
        margin: 0;
        padding: 0;
        width: 125px;

        border: 0;
        height: 32px;
        border-radius: 8px;
    }

    .Mobile_AboutPageFooterBox {
        margin: 0;
        padding: 0;
        display: block;
    }
}